"use client";

import { GlassCard } from "@/components/ui/glass-card";
import { Upload, Sliders, Download } from "lucide-react";

const steps = [
  {
    icon: Upload,
    title: "Upload Images",
    description:
      "Select your reference image and the target image you want to color grade.",
  },
  {
    icon: Sliders,
    title: "Adjust Intensity",
    description:
      "Fine-tune the color transfer intensity to get the perfect balance.",
  },
  {
    icon: Download,
    title: "Download Result",
    description: "Save your color-graded image in high resolution.",
  },
];

export function HowItWorks() {
  return (
    <section className="py-20 px-4">
      <div className="max-w-6xl mx-auto">
        <h2 className="text-3xl md:text-4xl font-bold text-center mb-16 animate-fade-in">
          <span className="gradient-text">How It Works</span>
        </h2>

        <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
          {steps.map((step, index) => (
            <GlassCard
              key={step.title}
              className="p-6 transition-all duration-300 hover:scale-105 animate-fade-in"
              style={{ animationDelay: `${index * 100}ms` }}
            >
              <div className="flex flex-col items-center text-center">
                <div className="mb-4 p-3 rounded-full bg-primary/10">
                  <step.icon className="h-6 w-6 text-primary" />
                </div>
                <h3 className="text-xl font-semibold mb-2">{step.title}</h3>
                <p className="text-muted-foreground">{step.description}</p>
              </div>
            </GlassCard>
          ))}
        </div>
      </div>
    </section>
  );
}
