"use client";

import { useEffect, useRef } from "react";
import { cn } from "@/lib/utils";

interface AnimatedBackgroundProps {
  intensity?: "subtle" | "medium" | "strong";
  speed?: "slow" | "medium" | "fast";
  className?: string;
}

export function AnimatedBackground({
  intensity = "subtle",
  speed = "medium",
  className,
}: AnimatedBackgroundProps) {
  const canvasRef = useRef<HTMLCanvasElement>(null);

  useEffect(() => {
    const canvas = canvasRef.current;
    if (!canvas) return;

    const ctx = canvas.getContext("2d");
    if (!ctx) return;

    let animationFrameId: number;
    let time = 0;

    const resizeCanvas = () => {
      canvas.width = window.innerWidth;
      canvas.height = window.innerHeight;
    };

    const draw = () => {
      if (!ctx) return;

      const width = canvas.width;
      const height = canvas.height;

      // Clear canvas
      ctx.clearRect(0, 0, width, height);

      // Create gradient
      const gradient = ctx.createLinearGradient(0, 0, width, height);

      // Add color stops based on time
      const t = time * 0.001;
      const intensityValue =
        intensity === "subtle" ? 0.1 : intensity === "medium" ? 0.2 : 0.3;

      gradient.addColorStop(0, `rgba(255, 182, 193, ${intensityValue})`);
      gradient.addColorStop(0.5, `rgba(173, 216, 230, ${intensityValue})`);
      gradient.addColorStop(1, `rgba(255, 182, 193, ${intensityValue})`);

      // Draw gradient
      ctx.fillStyle = gradient;
      ctx.fillRect(0, 0, width, height);

      // Update time
      const speedValue = speed === "slow" ? 0.2 : speed === "medium" ? 0.5 : 1;
      time += speedValue;

      // Request next frame
      animationFrameId = requestAnimationFrame(draw);
    };

    // Initial setup
    resizeCanvas();
    draw();

    // Handle window resize
    window.addEventListener("resize", resizeCanvas);

    // Cleanup
    return () => {
      window.removeEventListener("resize", resizeCanvas);
      cancelAnimationFrame(animationFrameId);
    };
  }, [intensity, speed]);

  return (
    <canvas
      ref={canvasRef}
      className={cn(
        "fixed top-0 left-0 w-full h-full -z-10 pointer-events-none",
        className
      )}
    />
  );
}
